
















import {
  Component, Emit, Prop, Vue,
} from 'vue-property-decorator';

@Component
export default class Banner extends Vue {
  @Prop({ default: false }) link!: boolean;
  @Prop({ default: false }) skip!: boolean;

  @Emit()
  linkExec() {
    return '';
  }

  @Emit()
  skipExec() {
    return '';
  }
}
